import "./App.scss";
import CreateController from "./CreateController";
import ProfileController from "./ProfileController";
import ExploreController from "./ExploreController";
import {
  Navbar,
  Nav,
  Container,
  Form,
  FormControl,
  Button,
  Modal,
} from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleRight } from "@fortawesome/free-solid-svg-icons";
import DingocoinLogo from "./assets/img/dingocoin.png";
import React from "react";
import { BrowserRouter as Router, Route, Routes, Navigate } from "react-router-dom";
import { getMeta } from "./storage";

function App() {
  const [location, setLocation] = React.useState(null);
  React.useEffect(() => {
    setLocation(window.location.pathname);
  }, []);
  React.useEffect(() => {}, [location]);

  const [searchQuery, setSearchQuery] = React.useState("");
  const [searchErrorShow, setSearchErrorShow] = React.useState(null);

  const doSearch = async (e) => {
    e.preventDefault();
    e.stopPropagation();
    e.nativeEvent.stopImmediatePropagation();

    setSearchQuery(searchQuery.trim());

    if (searchQuery.trim() !== "") {
      const nftMeta = await getMeta(searchQuery.trim());
      if (nftMeta !== null) {
        window.location.assign(`/nft/${searchQuery.trim()}`);
      } else {
        window.location.assign(`/profile/${searchQuery.trim()}`);
      }
    }
  };

  return (
    <Router>
      <div className="App">
        <Navbar className="navbar" bg="dark" expand="lg" sticky="top">
          <Container>
            <Navbar.Brand href="/" className="navbar-brand align-items-center">
              <img alt="" src={DingocoinLogo} />
              <span>DINGOCOIN</span>
              <span className="navbar-brand-subtitle"> NFT Platform</span>
            </Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse>
              <Nav
                className="ms-auto"
                style={{ paddingRight: "1.5rem", textAlign: "center" }}
              >
                <Nav.Link href="/explore">Explore</Nav.Link>
                <Nav.Link href="/create">Create</Nav.Link>
              </Nav>
              <Form className="d-flex" onSubmit={doSearch}>
                <FormControl
                  type="search"
                  placeholder="Jump to NFT/profile address..."
                  aria-label="Search"
                  style={{
                    borderTopRightRadius: "0",
                    borderBottomRightRadius: "0",
                    minWidth: "20rem",
                  }}
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                />
                <Button
                  variant="secondary mx-0"
                  style={{
                    borderTopLeftRadius: "0",
                    borderBottomLeftRadius: "0",
                  }}
                  type="submit"
                >
                  <FontAwesomeIcon icon={faAngleRight}></FontAwesomeIcon>
                </Button>
              </Form>
            </Navbar.Collapse>
          </Container>
        </Navbar>

        <section className="section-content">
          <Routes>
            <Route
              path="/profile/:profileAddress"
              element={<ProfileController />}
            />
            <Route
              path="/profile/:profileAddress/owned"
              element={<ProfileController />}
            />
            <Route
              path="/collection/:collectionHandle"
              element={<ProfileController />}
            />
            <Route path="/nft/:nftAddress" element={<ProfileController />} />
            <Route
              path="/nft/:nftAddress/owned"
              element={<ProfileController />}
            />
            <Route path="/explore" element={<ExploreController />} />
            <Route path="/explore/top" element={<ExploreController />} />
            <Route path="/explore/all" element={<ExploreController />} />
            <Route path="/create" element={<CreateController />} />
            <Route path="*" element={<Navigate to="/explore"/>}/>
          </Routes>
        </section>
        <section>
          <div className="justify-content-center section-footer align-items-center">
            <h6>Copyright © The Dingocoin Project 2021-2022</h6>
          </div>
        </section>
      </div>

      <Modal
        size="md"
        centered
        show={searchErrorShow}
        onHide={() => setSearchErrorShow(false)}
      >
        <Modal.Header closeButton>
          <Modal.Title>NFT/profile not found</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Container className="text-center my-2">
            <p>No results found.</p>
            <Button className="mx-2" onClick={() => setSearchErrorShow(false)}>
              Close
            </Button>
          </Container>
        </Modal.Body>
      </Modal>
    </Router>
  );
}

export default App;
